      return store.state.search.routeList
    })
    const musicbtn = () => {
      router.push({
        path: '/main/discovr/music/recommend'
      })
    }
    const URLbtn = () => {
      router.push({
        path: '/main/online/video'
      })
    }
    // 使用计算属性 判断 路由路径 nav-meun 跳转到那个页面  值 defalutValue
    const defalutValue = computed(() => {
      return 1
    })
    return {
      musicbtn,
      aside,
      URLbtn,
      defalutValue
    }
  }
})
